.newestPost {
    > span {
        @apply capitalize text-xl mobile:text-base;
    }

    > h2 {
        @apply text-[42px] mobile:text-2xl leading-[48px] mobile:leading-[28px] font-secondary mt-4 mobile:mt-2 mb-5;
    }
}

.preview {
    @apply flex flex-wrap;

    p {
        @apply mobile:text-sm;

        & + p {
            @apply mt-6;
        }
    }

    h4 {
        @apply mt-10 mobile:mt-6 mb-6 text-2xl mobile:text-xl font-semibold;
    }
}

.postCard {
    @apply flex flex-col w-1/2 max-w-[538px] mobile:w-[48%] h-[234px] mobile:h-[242px] rounded-2xl p-4 shadow-popUp bg-white last:ml-6 mobile:last:ml-auto;

    span {
        @apply text-xs;
    }

    h4 {
        @apply text-xl mobile:text-base font-semibold mt-4;
    }
}
